<template>
   <div class="contadd animated fadeInDownBig">
	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px ">
    
   
		<el-form-item label="" prop="name">
			<el-input v-model="ruleForm.name"></el-input>
		</el-form-item>

    <el-form-item label="" prop="tel">
			<el-input v-model="ruleForm.tel"></el-input>
		</el-form-item>

		<el-form-item label="" prop="region">
			<el-select v-model="ruleForm.region" placeholder="">
				<el-option label="A" value="A"></el-option>
				<el-option label="B" value="B"></el-option>
        <el-option label="C" value="C"></el-option>
				<el-option label="D" value="D"></el-option>
        <el-option label="E" value="E"></el-option>
				<el-option label="F" value="F"></el-option>
			</el-select>
		</el-form-item>
    
		<el-form-item label="" required>
			<el-col :span="5">
				<el-form-item prop="date1">
					<el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
				</el-form-item>
			</el-col>
			<el-col class="line" :span="1" style="text-align: center;">_</el-col>
			<el-col :span="5">
				<el-form-item prop="date2">
					<el-time-picker type="fixed-time" placeholder="" value-format="HH:mm:ss"   format="HH:mm:ss" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
				</el-form-item>
			</el-col>
		</el-form-item>

    	<el-form-item label="" required prop="sjfw">
          <el-date-picker
            v-model="ruleForm.sjfw" value-format="yyyy-MM-dd"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
    </el-form-item>

  
  

		<!-- <el-form-item label="" prop="delivery">
			<el-switch v-model="ruleForm.delivery"></el-switch>
		</el-form-item> -->

		<el-form-item label="" prop="type">
		  	<el-checkbox-group v-model="ruleForm.type" @change="test" >
          <el-checkbox v-for="item in ruleForm.checkList" :key="item.label" :label="item.label">{{item.label}}</el-checkbox>
        </el-checkbox-group>
		</el-form-item>

		<el-form-item label="" prop="resource">
			<el-radio-group v-model="ruleForm.resource">
				<el-radio label=""></el-radio>
				<el-radio label=""></el-radio>
			</el-radio-group>
		</el-form-item>

		<el-form-item label="" prop="desc">
			<el-input type="textarea" v-model="ruleForm.desc"></el-input>
		</el-form-item>

		<el-form-item>
			<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
			<el-button @click="resetForm('ruleForm')"></el-button>
		</el-form-item>
	</el-form>

  </div>
</template>

<script>
import axios from "axios";
//
import { isvalidPhone } from "../../config/validate";
//
var validPhone = (rule, value, callback) => {
  if (!value) {
    callback(new Error(""));
  } else if (!isvalidPhone(value)) {
    callback(new Error("11"));
  } else {
    callback();
  }
};

export default {
  props: {
			title: {
				type: String
			}
		},
  data() {
    return {
      ruleForm: {
        
        name: "",
        region: "",
        tel: "",
        date1: "",
        date2: "",
        sjfw:[],
        delivery: false,
        type: [],
        checkList: [
          {
            id: "001",
            label: "Vue.js"
          },
          {
            id: "002",
            label: "Angular.js"
          },
          {
            id: "002",
            label: "React.js"
          },
          {
            id: "002",
            label: "Node.js"
          },
          {
            id: "002",
            label: "PHP.js"
          }
        ],
        resource: "",
        desc: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 2,
            max: 5,
            message: " 3  5 ",
            trigger: "blur"
          }
        ],
        tel: [
          { required: true, trigger: "blur", validator: validPhone } //
        ],
        region: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        date1: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        date2: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        sjfw:[
          {
            type: "array",
            required: true,
            message: "-",
            trigger: "change"
          }
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        resource: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        desc: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 3,
            max: 20,
            message: " 3  20 ",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    test(test) {
      console.log(test);
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const data = {
            name: this.ruleForm.name,
            tel: this.ruleForm.tel,
            checked: this.ruleForm.type,
             xuanxiang: this.ruleForm.region,
             date1:this.ruleForm.date1,
              date2:this.ruleForm.date2,
              sjfw:this.ruleForm.sjfw,
            radio: this.ruleForm.resource,
            textarea: this.ruleForm.desc
          };
          console.log(data);
          axios.post("/users.json	", data).then(res => {
            this.$message({
              showClose: true,
              message: "^_^",
              type: "success"
            });
            setTimeout(() => {
                this.$router.push({name:'home'})
            }, 3000);
            console.log(res);
          });
        } else {
          console.log("error submit!!");
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>


<style scoped>
.contadd{
  padding: 2% ;
}
.el-form-item__label{
  text-align: center;
  font-weight: bold;
}
.shouye {
		padding-bottom: 10px;
		border-bottom: 2px solid rgb(205, 50, 26) !important;
	}


</style>



