<template>
	<div class="bj_zhuce">
	<div class="container bao_zhuce animated flipInY">
      <h1 class="ld"></h1>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm form_zhuce">
                <el-form-item label="" prop="name">
                  <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
              
                <el-form-item label="" prop="pass">
                  <el-input type="password" v-model="ruleForm.pass"></el-input>
                </el-form-item>
                
                <el-form-item label="" prop="checkPass">
                  <el-input type="password" v-model="ruleForm.checkPass"></el-input>
                </el-form-item>

                <el-form-item label="" prop="youxiang">
                  <el-input v-model="ruleForm.youxiang"></el-input>
                </el-form-item>
                
                 <el-form-item label="" prop="tel">
                  <el-input v-model="ruleForm.tel"></el-input>
                </el-form-item>
                
                <el-form-item label="" prop="xb">
                  <el-radio-group v-model="ruleForm.xb">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                  </el-radio-group>
                </el-form-item>
                
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                  <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
        </el-form>
	</div>
	</div>
</template>
<script>
//
import { isvalidPhone } from "../../config/validate";

import axios from 'axios'

export default {
  data() {
    //
    var validPhone = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else if (!isvalidPhone(value)) {
        callback(new Error("11"));
      } else {
        callback();
      }
    };

    var validateEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (value !== "") {
          var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
          if (!reg.test(value)) {
            callback(new Error(""));
          }
        }
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        name: "",
        pass: "",
        checkPass: "",
        youxiang: "",
        tel: "",
        xb: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        pass: [{ validator: validatePass, trigger: "blur",required: true, }],
        checkPass: [{ validator: validatePass2, trigger: "blur",required: true, }],
        youxiang: [
          { required: true, trigger: "blur", validator: validateEmail } //
        ],
        tel: [
          { required: true, trigger: "blur", validator: validPhone } //
        ],
        xb: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
         
          const data = {
            name: this.ruleForm.name,
            mima: this.ruleForm.pass,
            checkPass: this.ruleForm.checkPass,
             youxiang: this.ruleForm.youxiang,
            tel: this.ruleForm.tel,
            xb: this.ruleForm.xb
          };
          console.log(data);
          axios.post("/yhxx.json	", data).then(res => {
            sessionStorage.setItem("name", this.ruleForm.name);
            sessionStorage.setItem("mima", this.ruleForm.pass);
            this.$message({
              message: "^_^",
              type: "success"
            });
            console.log(res);
            this.$router.push({ name: "login" });
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      // this.$refs[formName].resetFields();
      this.$router.push({ name: "login" });
    }
  }
};
</script>

<style>
.bj_zhuce {
  width: 100%;
  height: 100vh;
  background: url("http://demo.cssmoban.com/cssthemes4/cpts_928_bub/images/banner.jpg");
  background-size: cover;
}
.bao_zhuce {
  width: 432px;
  border-radius: 10px;
  height: auto;
  border: #28a745 solid 0px;
  margin: 10px 0;
  
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 445px;
  bottom: 0;
  margin: auto;
  font-size: 12px;
  color: white;
  background: white;
}
.el-form-item {
  margin-bottom: 20px;
}

.form_zhuce {
  margin-top: 20px;
  width: 397px;
}
.ld {
  text-align: center;
  line-height: 80px;
  color: #333;
}
</style>


