<template>
  <div class="deit" style="width:95%;margin:0 auto;" >
    <div class="crumbs" style="width:100%;margin:0 auto;">
      <el-breadcrumb separator="/">
            <el-breadcrumb-item><i class="el-icon-date"></i> </el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <div class="cantainer">
                    <el-table style="width: 100%;"
                    :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)"  
                    >
                        <el-table-column type="index" width="50">    
                        </el-table-column>
                        <el-table-column label="" prop="date" width="180">    
                        </el-table-column>
                        <el-table-column label="" prop="name" width="180">    
                        </el-table-column>
                        <el-table-column label="" prop="tel" width="180">    
                        </el-table-column>
                        <el-table-column label="" prop="address" width="200">    
                        </el-table-column>    
                    </el-table>
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10, 20, 40]" 
                            :page-size="pagesize"         
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="userList.length">   
                    </el-pagination>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      currentPage: 1, //
      pagesize: 10, //    
      userList: []
    };
  },
  created() {
    this.handleUserList();
  },
  methods: {
    // currentPagepagesizedata
    handleSizeChange: function(size) {
      this.pagesize = size;
      console.log(this.pagesize); //
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage;
      console.log(this.currentPage); //
    },
    handleUserList() {
     axios.get("/users.json").then(e => {
				// console.log(e.data);
					var arr = []
				for(let key in e.data) {
				
					e.data[key].index = key;
					// console.log(e.data[key].index)
					
					// this.msg.push(e.data[key]);
				 arr.push(e.data[key])
					
				}
      
        this.userList = arr;

			});
    }
  }
};
</script>




