<template>
	<div class="sy_bao animated fadeIn">
		<div id="myChart" :style="{width: '800px', height: '800px'}" style="float:left"></div>
		<div id="myChart2" :style="{width: '800px', height: '800px'}" style="float:right"></div>
	</div>
</template>
<script>
	// 
	let echarts = require("echarts/lib/echarts");
	// 
	require("echarts/lib/chart/bar");
	// title
	require("echarts/lib/component/tooltip");
	require("echarts/lib/component/title");
	export default {
		name: "hello",
		data() {
			return {
				msg: "Welcome to Your Vue.js App"
			};
		},
		mounted() {
			this.drawLine();
		},
		methods: {
			drawLine() {
				// domecharts
				let myChart = this.$echarts.init(document.getElementById('myChart'))
				let myChart2 = this.$echarts.init(document.getElementById('myChart2'))
				// 
				myChart.setOption({
					title: {
						// text: '',
						// subtext: ''
					},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						}
					},
					legend: {
						data: ['2019']
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					xAxis: {
						type: 'value',
						boundaryGap: [0, 0.01]
					},
					yAxis: {
						type: 'category',
						data: ['css3', 'Vue.js', 'javascript', 'Photoshop', 'Jquery', 'html5']
					},
					series: [
						{
							name: '2019',
							type: 'bar',
							data: [80, 80, 85, 90, 95, 95]
						}
					]
				});

				myChart2.setOption({
					title: {
						
						
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					legend: {
						// orient: 'vertical',
						left: 'center',
						data: ['', '', '', '']
					},
					series: [{
						name: '',
						type: 'pie',
						radius: '55%',
						center: ['50%', '50%'],
						data: [{
								value: 90,
								name: ''
							},
							{
								value: 60,
								name: ''
							},
							{
								value: 50,
								name: ''
							},
							{
								value: 90,
								name: ''
							}
						],
						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				});

			}
		}
	};
</script>

<style>
	.sy_bao {
		background: #fff;
		height: 500px;
		padding: 0px;
	}
</style>


